    document.getElementById("append").addEventListener("click", async () => {
        return Excel.run(async function(context) {
            let appendText= document.querySelector("#appendData").value;

            const sheet = context.workbook.worksheets.getActiveWorksheet();    

            const table = context.workbook.tables.getItem("DataTable")
            const tableData = table.getDataBodyRange().load('values');

            const selRange = context.workbook.getSelectedRange().getEntireColumn().load(["columnIndex", 'address'])

            await context.sync()

            const index = selRange.columnIndex;
            const addr = selRange.address;

            let newTableData = []
            for (var i = 0; i < tableData.values.length; i++) {
                let row = tableData.values[i];
                let alteredData = row.map((cell, i) => {
                    if (i === index) {
                        return `${cell}${appendText}`
                    } else {
                        return cell
                    }
                })

                newTableData = newTableData.concat([alteredData])

            }
            tableData.values = newTableData;
            
            await context.sync();

        });
    });

