    document.getElementById("artworkCol").addEventListener("click", async () => {
        return Excel.run(async function(context) {
            let dirText= document.querySelector("#artworkData").value;

            const sheet = context.workbook.worksheets.getActiveWorksheet();    

            const table = context.workbook.tables.getItem("DataTable")
            const tableData = table.getDataBodyRange().load('values');

            const selRange = context.workbook.getSelectedRange().getEntireColumn().load(["columnIndex", 'address'])

            await context.sync()

            const index = selRange.columnIndex;
            const addr = selRange.address;

            let newTableData = []
            for (var i = 0; i < tableData.values.length; i++) {
                let row = tableData.values[i];
                let alteredData = row.map((cell, j) => {
                // Check if current cell (j) needs modification (based on your index condition)
                if (j === index) {
                    // Split the cell at the dot (.)
                    const parts = cell.split('.');
                    // Lowercase everything after the first dot
                    parts[1] = parts[1] ? parts[1].toLowerCase() : ''; // Handle empty strings after dot
                    // Join the parts back together
                    return `${dirText}${parts.join('.')}`;
                } else {
                    return cell;
                }
                });
                // Update the row with the modified data
                newTableData = newTableData.concat([alteredData])
            }
                    
            
            tableData.values = newTableData;
            
            await context.sync();

            // Get the first table on this sheet
            let activeTable = sheet.tables.getItemAt(0);

            // Get the headers from the table
            let headerRangeValues = activeTable.getHeaderRowRange().load("values");
            await context.sync();
            let newHeaderData = []

            for (var i = 0; i < headerRangeValues.values.length; i++) {
                let row = headerRangeValues.values[i];

                let alteredHeaders = row.map((cell, j) => {
                // Check if current cell (j) needs modification (based on your index condition)
                if (j === index) {
                    return `'@${cell}`;
                } else {
                    return cell;
                }
                });
                // Update the row with the modified data
                newHeaderData = newHeaderData.concat([alteredHeaders])
            }
            headerRangeValues.values = newHeaderData;
            
            await context.sync();
        });
    });
                                                            
